export default {
	data() {
		return {
			card:{
				id: '1231123',
				img: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=4162264519,731856317&fm=26&gp=0.jpg',
				name: '麻辣烫'
			}
		}
	},
	onLoad() {
		uni.getStorage({
			key: 'UserId',
			success: (res) => {
				console.log(res)
				this.userid = res.data
				let id = res.data
				uni.request({
					url:'http://ygc.huiqian99.com/api/index/getGoods',
					data:{
						id:id
					},
					success: (e) => {
						// var 
						console.log('this food',e)
					}
				})
			}
		})
	},
	onShow() {
	},
	methods: {
		goShopList(e) {
			console.log(e.currentTarget.id)
			uni.setStorageSync('fondNames', e.currentTarget.id)
			uni.navigateTo({
				url: 'shopList',
			});
		},
		likes(e) {
			console.log('thisId', e.currentTarget.id)
			uni.showToast({
				title: 'yes you like'
			});
			uni.showLoading({
				title: 'Loading',
				mask: true
			});
		},
		hates(e) {
			console.log('thisId', e.currentTarget.id)
			uni.showToast({
				title: 'no you hates'
			});
			uni.showLoading({
				title: 'Loading',
				mask: true
			});
		}
	}
}
